
html {
  font-size: 16px;
}


*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

.p-10{
  padding: 5px 10px;
}

.f-16{
  font-size: 16px;
}

.color-grey{
  color: gray;
}

.flex-sb{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.flex-gap-10{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 10px;
}

.relative{
  position: relative;
}



.header-section {
  margin-bottom: .5rem;
}

.main-section {
  padding: 1rem;
  border-radius: 5px;
  background-color: lightcyan;
  height: 100%;
}


.flex-lc {
  display: flex;
  justify-content: left;
  align-items: center;
}

.flex-cc {
  display: flex;
  justify-content: center;
  align-items: center;
}

.flex-sc {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.flex-ct {
  display: flex;
  justify-content: left;
}

.main-container {
  padding: 1rem;
}

.p {
  max-width: 200px;
}