

.card-container {
    /* height: 5.5rem; */
    width: 14.5rem;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    padding: .25rem;
    background-color: white;
    margin-bottom: .5rem;
}


.first-row {
    padding: .1rem;
}

.cam {
    color: grey;
    font-size: .75rem;
}

.user-div {
    font-size: 1.25rem;
}


.second-row {
    padding-bottom: .5rem;


}

.status-div {
    margin-right: 0.6rem;

}

p{
    font-size: .75rem;
    text-overflow: ellipsis;
    max-lines: 2;
}







.third-row {
    padding: .1rem;
    font-size: .75rem;
}

.priority-div{
    border: 1px solid cyan;
    margin-right: .6rem;
    height: 1.25rem;
    padding: 0 .6rem;
    border-radius: 5px;

}

.priority-div img {
    height: .75rem;
}

.feature-div {
    border: 1px solid cyan;
    height: 1.25rem;
    /* font-size: 1rem; */
    color: darkcyan;
    border-radius: 5px;
    padding-right: .5rem;
}

.dot {
    font-size: 1.5rem;
    color: grey;

}
